.language-selector {
    display: flex;
    gap: 5px;  
  
    button {
      border: none;
      background: none;
      padding: 0.5rem;
      margin: 0 0.5rem;
  
      img {
        width: 40px;   
        height: 20px;  
        cursor: pointer;
      }
    }
  }
  
  @media (max-width: 480px) {
    
    .language-selector{
        display: flex;
        justify-content: center;
        align-items: center;

        button {
            
            img {
            width: 35px;   
            height: 17px;  
            cursor: pointer;
          }
    }
  }
}