.use_technology {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  width: 100%;
  height: 100%;
}
.use_technology .title {
  color: white;
  align-items: center;
  margin: 20px;
}
.use_technology .title h2 {
  display: flex;
  justify-content: flex-start;
}
.use_technology .icons {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.use_technology .icons img {
  display: flex;
  width: 70px;
  height: 70px;
  margin: 0 10px;
  justify-content: space-around;
}
.use_technology .icons img:hover {
  scale: 1.2;
}/*# sourceMappingURL=UseTechnologyStyle.css.map */