// Uvoz stilova
@import "../../../src/components/colors.scss";
@import "../../components/Fonts.scss";
@import "../../components/Animacije/ColorText.css";

// Glavni selektor za početnu stranicu
.home-page {
  display: flex;
  flex-direction: column;

  // Selektor za naslov
  .title {
    display: flex;
    flex-direction: column;
    main {
      background-image: url('../../images/header.jpg');
      background-repeat: no-repeat;
      background-size: cover;
      width: 100%;
      height: 550px;
      padding: 10px;
      display: flex;
      flex-direction: column;
      justify-content: center;

      h1 {
        text-transform: uppercase;
        font-family: "Roboto mono", sans-serif;
        padding-bottom: 20px;
        font-size: 46px;
        margin: 40px 0px 20px 30px;
        color: $color-white;

        span {
          text-transform: capitalize;
          color: $color-green;
          margin: 0px 0px 20px 130px;
        }
      }

      section {
        display: flex;
        flex-direction: column;
        align-items: center;

        h2 {
          text-transform: uppercase;
          color: white;
          font-family: "Roboto mono", sans-serif;
          padding-bottom: 20px;
          font-size: 24px;
        }

        ul {
          list-style: none;

          li {
            color: $color-white;
            font-size: 15px;
            font-family: Arial, Helvetica, sans-serif;
            font-weight: 300;
            padding: 6px;
            letter-spacing: 2px;
            text-transform: lowercase;

            &:nth-child(1) {
              margin-left: -50px;
              animation: colorChange 4s linear 1;
            }

            &:nth-child(2) {
              margin-left: -40px;
              animation: colorChange 4s linear 0.5s 1;
            }

            &:nth-child(3) {
              margin-left: -30px;
              animation: colorChange 4s linear 1s 1;
            }

            &:nth-child(4) {
              margin-left: -20px;
              animation: colorChange 4s linear 1.5s 1;
            }

            &:nth-child(5) {
              margin-left: -10px;
              animation: colorChange 4s linear 2s 1;
            }
          }
        }
      }
    }

    // Selektor za dugmadi ispod naslova
    .home-buttons {
      display: flex;
      justify-content: center;
      align-items: flex-end;
      margin-top: auto;

      // Stil za kripto dugme
      .crypto_btn {
        a {
          text-decoration: none;
          font-size: 18px;
          font-weight: 600;
          color: #fff;
        }
        width: 160px;
        cursor: pointer;
        margin: 20px;
        height: 45px;
        text-align: center;
        border: none;
        background-size: 300% 100%;
        border-radius: 50px;
        transition: all 0.4s ease-in-out;

        &:hover {
          background-position: 100% 0;
          transition: all 0.4s ease-in-out;
        }

        &:focus {
          outline: none;
        }

        &.crypto_btn {
          background-image: linear-gradient(
            to right,
            $color-green,
            #3cba92,
            #30dd8a,
            #2bb673
          );
          box-shadow: 0 4px 15px 0 rgba(23, 168, 108, 0.75);
        }
      }

      // Stil za "O nama" dugme
      .about_btn {
        a {
          text-decoration: none;
          font-size: 18px;
          font-weight: 600;
          color: #fff;
        }
        width: 160px;
        cursor: pointer;
        margin: 20px;
        height: 45px;
        text-align: center;
        border: none;
        background-size: 300% 100%;
        border-radius: 50px;
        transition: all 0.4s ease-in-out;

        &:hover {
          background-position: 100% 0;
          transition: all 0.4s ease-in-out;
        }

        &:focus {
          outline: none;
        }

        &.about_btn {
          background-image: linear-gradient(
            to right,
            $color-green,
            #3cba92,
            #30dd8a,
            #2bb673
          );
          box-shadow: 0 4px 15px 0 rgba(73, 100, 89, 0.75);
        }
      }
    }
  }

  // Selektor za sekciju "useTec"
  .useTec {
    width: 100%;
    height: 300px;
    background-color: $color-black;
  }
}
