@import "../../colors.scss";

.use_technology{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    width: 100%;
    height: 100%;
    
    .title{
        color: white;
        align-items: center;
        margin: 20px;
        h2{
            display: flex;
            justify-content: flex-start;
            }
    }
    .icons{
        display: flex;
        flex-wrap: wrap;
        justify-content: center;

        img{
            display: flex;
            width: 70px;
            height: 70px;
            margin: 0 10px; 
            justify-content: space-around;

               &:hover{
                scale: 1.2;
            }
        }
    }
    
}