@import url("https://fonts.googleapis.com/css2?family=Roboto+Mono:ital,wght@0,100;0,400;1,700&family=Roboto:ital,wght@0,400;0,900;1,100&family=Tilt+Prism&display=swap");
@import "../../components/Animacije/ColorText.css";
.home-page {
  display: flex;
  flex-direction: column;
}
.home-page .title {
  display: flex;
  flex-direction: column;
}
.home-page .title main {
  background-image: url("../../images/header.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: 550px;
  padding: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.home-page .title main h1 {
  text-transform: uppercase;
  font-family: "Roboto mono", sans-serif;
  padding-bottom: 20px;
  font-size: 46px;
  margin: 40px 0px 20px 30px;
  color: #fdfdfd;
}
.home-page .title main h1 span {
  text-transform: capitalize;
  color: rgb(135, 255, 55);
  margin: 0px 0px 20px 130px;
}
.home-page .title main section {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.home-page .title main section h2 {
  text-transform: uppercase;
  color: white;
  font-family: "Roboto mono", sans-serif;
  padding-bottom: 20px;
  font-size: 24px;
}
.home-page .title main section ul {
  list-style: none;
}
.home-page .title main section ul li {
  color: #fdfdfd;
  font-size: 15px;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 300;
  padding: 6px;
  letter-spacing: 2px;
  text-transform: lowercase;
}
.home-page .title main section ul li:nth-child(1) {
  margin-left: -50px;
  animation: colorChange 4s linear 1;
}
.home-page .title main section ul li:nth-child(2) {
  margin-left: -40px;
  animation: colorChange 4s linear 0.5s 1;
}
.home-page .title main section ul li:nth-child(3) {
  margin-left: -30px;
  animation: colorChange 4s linear 1s 1;
}
.home-page .title main section ul li:nth-child(4) {
  margin-left: -20px;
  animation: colorChange 4s linear 1.5s 1;
}
.home-page .title main section ul li:nth-child(5) {
  margin-left: -10px;
  animation: colorChange 4s linear 2s 1;
}
.home-page .title .home-buttons {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  margin-top: auto;
}
.home-page .title .home-buttons .crypto_btn {
  width: 160px;
  cursor: pointer;
  margin: 20px;
  height: 45px;
  text-align: center;
  border: none;
  background-size: 300% 100%;
  border-radius: 50px;
  transition: all 0.4s ease-in-out;
}
.home-page .title .home-buttons .crypto_btn a {
  text-decoration: none;
  font-size: 18px;
  font-weight: 600;
  color: #fff;
}
.home-page .title .home-buttons .crypto_btn:hover {
  background-position: 100% 0;
  transition: all 0.4s ease-in-out;
}
.home-page .title .home-buttons .crypto_btn:focus {
  outline: none;
}
.home-page .title .home-buttons .crypto_btn.crypto_btn {
  background-image: linear-gradient(to right, rgb(135, 255, 55), #3cba92, #30dd8a, #2bb673);
  box-shadow: 0 4px 15px 0 rgba(23, 168, 108, 0.75);
}
.home-page .title .home-buttons .about_btn {
  width: 160px;
  cursor: pointer;
  margin: 20px;
  height: 45px;
  text-align: center;
  border: none;
  background-size: 300% 100%;
  border-radius: 50px;
  transition: all 0.4s ease-in-out;
}
.home-page .title .home-buttons .about_btn a {
  text-decoration: none;
  font-size: 18px;
  font-weight: 600;
  color: #fff;
}
.home-page .title .home-buttons .about_btn:hover {
  background-position: 100% 0;
  transition: all 0.4s ease-in-out;
}
.home-page .title .home-buttons .about_btn:focus {
  outline: none;
}
.home-page .title .home-buttons .about_btn.about_btn {
  background-image: linear-gradient(to right, rgb(135, 255, 55), #3cba92, #30dd8a, #2bb673);
  box-shadow: 0 4px 15px 0 rgba(73, 100, 89, 0.75);
}
.home-page .useTec {
  width: 100%;
  height: 300px;
  background-color: rgba(0, 0, 0, 0.899);
}/*# sourceMappingURL=HomePageStyle.css.map */